/* Footer 
 -------------------------*/

.contacts-card
  padding: 4rem
  max-width: 460px
  background: rgba(255,255,255,.7)
  font-size: 16px
  font-weight: 500
  border-radius: 5px

  &__header,
  &__body
    margin-bottom: 5rem

  &__item
    display: flex
    align-items: center
    margin-bottom: 0.6rem
    padding-top: 1.4rem
    padding-bottom: 1.4rem
    border-bottom: dashed 1px #C9C9C9

  &__icon
    margin-right: 1rem

  &__footer
    display: flex
    align-items: center

  &__social
    width: 40px
    height: 41px
    &:not(:first-child)
      margin-left: 1.6rem

.icon-rounded
  display: inline-flex
  justify-content: center
  align-items: center
  flex: none
  width: 4.7rem
  height: 4.7rem
  border-radius: 50%
  border: dashed 1px #C9C9C9

.map
  position: absolute
  width: 100%
  height: 100%
  top: 0
  right: 0
  bottom: 0
  left: 0
  z-index: 0
  &-placement
    position: relative
    padding-top: 10rem
    padding-bottom: 10rem
    height: 70rem
    &__container
      position: relative
      // padding: 0
      margin-left: calc(50vw - 725px)
      display: inline-flex

.marker-class
  display: block
  width: 200px
  height: 200px
  background: red
  border: solid 5px #000

.site-dev
  padding-top: 1.6rem
  padding-bottom: 1.6rem
  &__link
    font-family: $ff-primary
    font-size: 16px
    font-weight: 500
    line-height: 28px
    color: #555555

/* Modal 
 -------------------------*/
.modal
  width: 100%
  height: 100%
  position: fixed
  top: 0
  right: 0
  bottom: 0
  left: 0
  z-index: -1
  display: none
  justify-content: center
  align-items: center
  opacity: 0
  &.shown
    opacity: 1
    z-index: 9999
    .modal__inner
      // Транисформация для анимации
      transform: translate(0)
  &__inner
    display: flex
    flex-direction: column
    position: relative
    z-index: 100
    margin: 1.5rem
    padding: 7rem 5rem
    min-width: 290px
    max-width: 104rem
    min-height: 400px
    max-height: 80vmin
    border-radius: .5rem
    box-shadow: 0 5px 20px 2px rgba(0, 0, 0, .35)
    background: $white
    transform: translateY(25px)
    transition: inherit
  &__content
    display: flex
    flex-direction: column
    height: 100%
    overflow: hidden
  &__header
    padding-bottom: 0.75rem
  &__body
    padding-top: 0.75rem
    flex: 1
    overflow-x: hidden
    overflow-y: auto

  &-backdrop
    display: block
    position: absolute
    top: 0
    right: 0
    bottom: 0
    left: 0
    z-index: 1
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.8)

  &__close
    position: absolute
    top: -22px
    right: -22px
    cursor: pointer
    width: 20px
    height: 20px
    background: none
    &::before,
    &::after
      content: ''
      display: inline-block
      width: 100%
      height: 3px
      position: absolute
      left: 0
      right: 0
      background: $white
      border-radius: 4px
      transition: .2s
    &::before
      transform: rotate(45deg)
    &::after
      transform: rotate(-45deg)
    // &:hover
    //   &::before,
    //   &::after
    //     background: lighten(#000, 50%)

