}

.d-block {
  display: block;
}
.d-inline {
  display: inline;
}
.d-none {
  display: none;
}

.row {
  margin: 0 -1.5rem;
  display: flex;
  flex-wrap: wrap;
}

.scroll-hidden {
  padding-right: 17px;
  background-color: #f1f1f1;
  overflow: hidden;
}

