}

.text_lite {
  color: #ffffff;
}
.text-center {
  text-align: center;
}

a, a:visited {
  text-decoration: none;
  outline: none;
  color: #373737;
  transition: color 0.1s ease-in-out;
}
a.text-link {
  text-decoration: underline;
}
a.text-link:hover, a.text-link:active {
  color: #C1272D;
  text-decoration: underline;
}
