 * --------------------------------------- */
/* ВСПОМОГАТЕЛЬНЫЕ КЛАССЫ */
.text-content_light {
  color: #fff;
}

.has-bg {
  position: relative;
}

.bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.bg img {
  display: block;
  width: 100%;
  height: 100%;
}
.bg_cover img {
  object-fit: cover;
}
.bg_contain img {
  object-fit: contain;
}

.preloader {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
