/* Modal 
 * -------------------------*/
.modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  display: none;
  justify-content: center;
  align-items: center;
  opacity: 0;
}
.modal.shown {
  opacity: 1;
  z-index: 9999;
}
.modal.shown .modal__inner {
  transform: translate(0);
}
.modal__inner {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 100;
  margin: 1.5rem;
  padding: 7rem 5rem;
  min-width: 290px;
  max-width: 104rem;
  min-height: 400px;
  max-height: 80vmin;
  border-radius: 0.5rem;
  box-shadow: 0 5px 20px 2px rgba(0, 0, 0, 0.35);
  background: #ffffff;
  transform: translateY(25px);
  transition: inherit;
}
.modal__content {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}
.modal__header {
  padding-bottom: 0.75rem;
}
.modal__body {
  padding-top: 0.75rem;
  flex: 1;
  overflow-x: hidden;
  overflow-y: auto;
}
.modal-backdrop {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}
.modal__close {
  position: absolute;
  top: -22px;
  right: -22px;
  cursor: pointer;
  width: 20px;
  height: 20px;
  background: none;
}
.modal__close::before, .modal__close::after {
  content: "";
  display: inline-block;
  width: 100%;
  height: 3px;
  position: absolute;
  left: 0;
  right: 0;
  background: #ffffff;
  border-radius: 4px;
  transition: 0.2s;
}
.modal__close::before {
  transform: rotate(45deg);
}
.modal__close::after {
  transform: rotate(-45deg);
}

