 * -------------------------*/
/* ANIMATIONS
 * ------------------------------  */
.fake-class-to-add-import-rule {
  visibility: inherit;
}

.d-anim-delay-1000 {
  animation-delay: 1000ms;
}
.d-anim-delay-1100 {
  animation-delay: 1100ms;
}
.d-anim-delay-1200 {
  animation-delay: 1200ms;
}

.slideUpIn {
  animation-name: slideUpIn;
}

/* VI. Animations
 * --------------------------------------- */
@keyframes fadeIn {
  from {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    visibility: visible;
    opacity: 1;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
@keyframes slideUpIn {
  from {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(10%);
    transform: translateY(10%);
  }
  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
  }
}
@keyframes slideUpOut {
  from {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
  }
  to {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(-10%);
    transform: translateY(-10%);
  }
}
/* VII. Media-queries
 * --------------------------------------- */
/* XL */
@media (max-width: 1399.98px) {
  .container, .container_xl, .container_lg, .container_md, .container_sm {
